import React from 'react';

const AboutPage = ({ data }) => (
  <div>
    <h1>About {data.site.siteMetadata.name}</h1>
    <p>I build things. Currently, I <strong>am available</strong> for hiring. Contact me and we'll talk.</p>
  </div>
)

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        name
      }
    }
  }
`

export default AboutPage


